}
  @-webkit-keyframes light{
    0%{
      color: #454647
    }
    33%{
      color: #F2F3F5;
      text-shadow: 0 0 20px #F2F3F5
    }
    100%{
      color: #454647
    }
  }
  @keyframes light{
    0%{
      color: #454647
    }
    33%{
      color: #F2F3F5;
      text-shadow: 0 0 20px #F2F3F5
    }
    100%{
      color: #454647
    }
  }
  @-webkit-keyframes inverse{
    0%{
      color: #F2F3F5;
    }
    33%{
      color: #454647;
      text-shadow: 0 0 20px #454647
    }
    100%{
      color: #F2F3F5
    }
  }
  @keyframes inverse{
    0%{
      color: #F2F3F5;
    }
    33%{
      color: #454647;
      text-shadow: 0 0 20px #454647
    }
    100%{
      color: #F2F3F5
    }
  }
  .strelki span:nth-child(1){
    -webkit-animation: light 2s infinite;
            animation: light 2s infinite;
  }
  .strelki span:nth-child(2){
    -webkit-animation: light 2s infinite;
            animation: light 2s infinite;
    -webkit-animation-delay: 0.67s;
            animation-delay: 0.67s
  }
  .strelki span:nth-child(3){
    -webkit-animation: light 2s infinite;
            animation: light 2s infinite;
    -webkit-animation-delay: 1.34s;
            animation-delay: 1.34s
  }
  .strelki.inverse span:nth-child(1){
    -webkit-animation: inverse 2s infinite;
            animation: inverse 2s infinite;
  }
  .strelki.inverse span:nth-child(2){
    -webkit-animation: inverse 2s infinite;
            animation: inverse 2s infinite;
    -webkit-animation-delay: 0.67s;
            animation-delay: 0.67s
  }
  .strelki.inverse span:nth-child(3){
    -webkit-animation: inverse 2s infinite;
            animation: inverse 2s infinite;
    -webkit-animation-delay: 1.34s;
            animation-delay: 1.34s
  }
  .harryup{
    -webkit-animation: text 1s infinite;
            animation: text 1s infinite;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    color: red
  }
  @-webkit-keyframes text{
    100%{
